.shop-card {
  z-index: 8;
  box-sizing: border-box;
  background-color: $colorBackground;
  height: 100vh;
  width: 100%;
  overflow-y: auto;

  padding-top: 15px;
  padding-bottom: 20px;

  @include on-tablet {
    padding-top: 23px;
    padding-bottom: 30px;
  }

  @include on-desktop {
    padding-top: 40px;
    padding-bottom: 81px;
  }

  &-info-content {
    @include on-desktop {
      padding-top: 50px;
      display: flex;
      flex-direction: column-reverse;
      justify-content: flex-end;
    }
  }

  &__content {
    padding-top: 20px;
    display: flex;
    flex-direction: column;

    @include on-tablet {
      padding-top: 30px;
    }

    @include on-desktop {
      padding-top: 50px;
    }

    &-wrapper {
      display: flex;
      flex-direction: column;

      @include on-tablet {
        flex-direction: column-reverse;
      }
    }

    &-title {
      @extend %poppins-600;

      font-size: 18px;
      line-height: 150%;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      color: $colorBlack;
      margin-bottom: 10px;

      @include on-tablet {
        margin-bottom: 25px;
      }
    }

    &-price {
      @extend %poppins-400;

      font-size: 14px;
      line-height: 130%;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      color: $colorBlack;
      margin-bottom: 20px;

      @include on-tablet {
        margin-bottom: 10px;
        font-size: 18px;
        line-height: 150%;
        color: $colorGreen;
      }
    }

    &-weight {
      display: none;

      @extend %poppins-500;

      font-size: 18px;
      line-height: 150%;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      color: $colorBlack;
      margin-bottom: 30px;

      @include on-tablet {
        display: block;
      }
    }

    &-container {
      display: block;
      content: '';
      width: 100%;
      height: 248px;
      margin-bottom: 30px;
      background-repeat: no-repeat;
      background-size: cover;

      @include on-tablet {
        height: 440px;
        margin-bottom: 27px;
      }

      &--oil {
        background-image: url(../images/shop1.png);
      }

      &--ubtan {
        background-image: url(../images/ubtah.png);
      }
    }

    &-button {
      margin-bottom: 50px;

      @include on-tablet {
        max-width: 280px;
        margin: 0 auto;
        margin-bottom: 60px;
      }
    }

    &-text {
      @extend %poppins-400;

      font-size: 14px;
      line-height: 150%;
      color: $colorBlack;
      margin-bottom: 20px;

      @include on-desktop {
        font-size: 18px;
        margin-bottom: 40px;
      }

      &--green {
        @extend %poppins-400;

        font-size: 14px;
        line-height: 150%;
        color: $colorGreen;

        @include on-desktop {
          font-size: 18px;
        }
      }
    }
  }
}
